.product__items {
  position: relative;
  a {
    text-decoration: none;
    color: $color_medium_black;
  }
  .product__item {
    position: relative;
    width: 250px !important;
    height: 340px;
    border: 1px solid $color_border;
    margin-right: 1.4%;
    color: $color_black;
    cursor: pointer;
    -webkit-box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
    box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
    transition: all 1s;
    &:hover {
      -webkit-box-shadow: none;
      -moz-box-shadow: none;
      box-shadow: none;
      .item__img {
        .img__overlay {
          opacity: 0.8;
        }
      }
      .sub__info {
        top: -14%;
        visibility: visible;
        opacity: 1;
      }
    }
    &:last-child:hover {
      .sub__info {
        top: -14%;
        left: -165%;
        visibility: visible;
        opacity: 1;
      }
    }
    &:last-child {
      .sub__info {
        left: -165%;
        &:before {
          left: 96%;
        }
      }
    }
    .item__img {
      position: relative;
      width: 100%;
      height: 160px;
      border-bottom: 2px solid $color_border;
      img {
        width: 100%;
        height: 100%;
      }
      .img__overlay {
        position: absolute;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        cursor: pointer;
        opacity: 0;
        transition: all 0.5s;
      }
    }
    .item__info {
      background-color: $color_white;
      padding: 5%;
      height: 45%;
      h5 {
        font-weight: bold;
        font-size: 0.9rem;
        margin: 0;
      }
      h6 {
        padding: 5% 0;
        font-size: 0.9rem;
        color: $color_light_black;
        margin: 0;
      }
      .rating {
        font-size: 0.9rem;
        i {
          color: $color_light_orange;
        }
        .score {
          margin: 2%;
          font-weight: bold;
        }
        .reviews {
          font-weight: 400;
        }
      }
      h4 {
        margin-top: 5%;
        font-size: 1.2rem;
        font-weight: bold;
        text-align: right;
      }
      .price {
        text-align: right;
        padding: 5% 1%;
        .old__price {
          margin-right: 2%;
          text-decoration: line-through;
          color: $color_light_black;
        }
        .price {
          font-size: 1.4rem;
          font-weight: bold;
          color: $color_red;
        }
      }
    }
    .best__seller {
      position: absolute;
      top: 2%;
      right: -4%;
      background-color: $color_dark_blue;
      font-weight: bold;
      color: $color_white;
      padding: 5px;
      height: 32px;
      &:before {
        content: " ";
        position: absolute;
        width: 0;
        height: 0;
        left: -40%;
        top: 0%;
        border-width: 16px 10px;
        border-style: solid;
        border-color: $color_dark_blue $color_dark_blue $color_dark_blue
          transparent;
      }
      &:after {
        content: " ";
        position: absolute;
        width: 0;
        height: 0;
        right: 0;
        top: 100%;
        border-width: 5px 5px;
        border-style: solid;
        border-color: #0c50be transparent transparent#0c50be;
      }
    }
    .sub__info {
      position: absolute;
      top: -30%;
      right: -170%;
      width: 350px;
      height: auto;
      background-color: $color_white;
    
      visibility: hidden;
      opacity: 0;
      transition: all 0.3s;
      z-index: 1000;
      -webkit-box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
      -moz-box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
      box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
      &:before {
        position: absolute;
        content: "";
        width: 20px;
        height: 20px;
        top: 45%;
        left: -1%;

        background: $color_white;
        transform: rotate(45deg);
        border-right: none;
        border-bottom: none;
        z-index: 10;
      }
      .info__content {
        .teacher {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
          }
          h6 {
            font-size: 0.9rem;
            color: $color_light_black;
            margin: 0;
            margin-left: 2%;
          }
        }
        h5 {
          font-weight: bold;
          margin: 4% 0 10% 0;
        }
        p {
          font-size: 0.9rem;
          color: $color_light_black;
        }
        .meta {
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: 0.9rem;
        }
        .add__cart {
          margin: 7% 0;
          @include btn__default(
            $color_blue,
            $color_white,
            none,
            100%,
            50px,
            1rem
          );
          &:active {
            background-color: $color_black;
          }
        }
        .go_cart {
          margin: 7% 0;
          @include btn__default(
            transparent,
            $color_red,
            1px solid $color_red,
            100%,
            50px,
            1rem
          );
          &:active {
            background-color: $color_red;
            color: $color_white;
          }
        }
        .wishlist {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .add__list {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            width: 50%;
            span {
              color: $color_light_black;
              font-size: 0.8rem;
              padding-left: 5%;
            }
            .heart {
              font-size: 1.1rem;
              height: 30px;
              width: 30px;
              line-height: 32px;
              border-radius: 50%;
              border: 1px solid $color_light_black;
              color: $color_light_black;
              text-align: center;
            }
            &:hover {
              .heart {
                background-color: rgb(80, 78, 78);
              }
            }
          }
          .price {
            font-size: 1.2rem;
            font-weight: bold;
          }
        }
      }
    }
  }
}
