.Course {
  // background-color: $bg-color;
  .search__box {
    padding-top: 20px;
    .search-form {
      .search__menu {
        display: flex;
        justify-content: center;
        align-items: center;
        li {
          margin-right: 5px;
          width: 100%;
          .course__category {
            width: 300px;
            height: 50px;
          }
          .status,
          .search__input {
            @extend .course__category;
          }
          .search__button {
            width: 200px;
            height: 50px;
            font-weight: 700;
            outline: none;
            border: none;
            font-style: $fs-6;
            background-color: $color-blue-3;
            color: $color-white-0;
          }
        }
      }
    }
  }
  .courses {
    .sort__filter {
      margin: 3% 0;
      .sort {
        span {
          border: 1px solid red;
          padding: 10px;
          cursor: pointer;
          margin-right: 12px;
          color: $color-blue-4;
          border-color: $color-blue-4;
        }
        .sort__select {
          border: 1px solid #666;
          padding: 0.7em;
        }
      }
      .filter {
        span:first-child {
          padding-right: 1.5rem !important;
          border-right: 1px solid #000;
        }
        span:last-child {
          padding-right: 1.5rem !important;
          padding-left: 1.5rem !important;
          color: red;
          cursor: pointer;
        }
      }
    }
    .sort__items {
      .card {
        &:hover{
          box-shadow: none;
        }
        overflow: hidden;
        box-shadow: -1px 2px 15px -4px rgba(0, 0, 0, 0.75);
        position: relative;
        border: 1px solid gray;
        border-radius: 10px;
        height: 100%;
        text-decoration: none;
        .card-img {
          width: 100%;
          height: 200px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .card-body,
        .button {
          padding: 10px 0;
          text-align: center;

          span {
            font-size: 20px;
            font-weight: bold;
            color: red;
            cursor: pointer;
          }
          .old__price {
            font-size: 15px;

            text-decoration: line-through;
            color: #aaaaaa;
          }
          .btn-addToCart {
            font-size: 30px;
          }
          .btn-goToCart{
           @extend .btn-addToCart
          }
        }
        .card-body{
          width: 100%;
          height: 70px;
        }

        .button {
          display: flex;
          justify-content: space-between;
          padding: 10px 10px;
        }

        .ratings {
          font-size: 20px;
          color: #ffce55;
        }

